/* Define variables for reusability */
$primary-color: #007bff;
$secondary-color: #333;

$background: #121735;
$text-color: #41a38a;
$file-hover-color: #182b54;

.AccountSettings {
    display: flex;
    width: 100%;
    flex-direction: column;
    color: $text-color;
    background-color: $background;
    padding: 12px;

    .accountItem {
        display: flex;
        flex-direction: column;
        padding: 2%;
        height: 50vh;
        align-items: center;

        .ItemTitle {
            margin-bottom: 100px;
        }

        button {
            box-sizing: border-box;
            color: $text-color;
            background-color: rgba(black, 0.7);
            border: none;
            border-radius: 10px;
            padding: 8px;
            margin: 2px;
            width: 100px;
            transition: all 0.3s;

            &:hover {
                background-color: rgba(white, 0.1);
            }
        }
    }

}