$sidebar-top: #040717;
$sidebar: #121735;
$sidebar-icons: #343a5c;

$button-accent: #41a38a;

.sidebar {
    min-width: 60px;
    max-width: 80px;
    background-color: $sidebar;
    color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    box-shadow: 2px 1px 5px 5px black inset;
    height: 100svh;


    .sidebar-top {
        height: 20vh;
        background-color: $sidebar-top;
        margin-bottom: 10vh;
        border-radius: 0 0 10px 10px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        overflow: hidden;
        user-select: none;

        img {
            width: 100%;
            border-radius: 100px;
            user-select: none;
        }
    }

    .navbar {
        display: flex;
        flex-direction: column;
        flex: 1;
        height: 100svh;
        overflow-y: auto;


        >* {
            flex: 1;
            cursor: pointer;
            position: relative;

            svg {
                margin-left: auto;
                margin-right: auto;
            }

            svg path {
                transition: fill 0.5s ease;
                fill: $sidebar-icons;
            }

            width: 70%;
            padding-left: 15%;
            padding-right: 15%;

            &:hover,
            &.active {
                svg path {
                    fill: $button-accent;
                }

                &::after {
                    background-color: $button-accent;
                }
            }

            &::after {
                content: "";
                transition: background-color 0.15s ease;
                position: absolute;
                top: 0;
                right: 0;
                border-radius: 10px;
                height: 100%;
                width: 5%;
            }
        }
    }
}

@media only screen and (max-width: 767px) {}