$primary-color: #007bff;
$secondary-color: #333;

$background: #121735;
$text-color: #41a38a;
$file-hover-color: #182b54;

#aboutPage{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: $background;
    width: 100%;
    height: 100%;

    display: flex;

    .aboutTitle{
        color: $text-color;
        margin: 2%;
    }

    .aboutBody{
        color: white;
        margin: 5% 20%;
        font-size: 1.2em;
    }
}