/* Define variables for reusability */
$primary-color: #007bff;
$secondary-color: #333;

$background: #121735;
$text-color: #41a38a;
$file-hover-color: #182b54;

.hidden{
    display: none;
}

.CreateFolderPopUp{
    position: absolute;
    padding: 10px;
    background-color: rgba(black, 0.9);
    backdrop-filter: blur(10px);
    border-radius: 10px;
    width: 50vw;

    input[type=text] {
        box-sizing: border-box;
        width: 100%;
        padding: 12px 5px;
        margin: 8px 0;
        border: none;
        outline: none;
    }

    button{
        box-sizing: border-box;
        width: 100%;
        color: $text-color;
        background-color: $background;
        border: none;

        :focus{
            background-color: black;
        }
    }
}

.content_container {
    width: 100%;
    height: 100vh;
    background-color: $background;

    display: flex;
    flex-direction: column;
    position: relative;

    align-items: center;

    .dirpath{
        display: inline-block;
        position: absolute;
        top: 10vh;
        width: fit-content;
        max-width: 90vw;
        height: 15px;
        padding: 4px;
        fill: rgba($text-color, 0.5);
        z-index: 99;
        background-color: rgba(black, 0.5);
        backdrop-filter: blur(10px);
        border-radius: 10px;
        overflow: hidden;
        text-overflow: ellipsis;
        color: $text-color;
        text-wrap: none;
        white-space: nowrap;

        path{
            fill: inherit;
        }
    }

    .undoMove{
        position: absolute;
        right: 5%;
        top: 0;
        width: 8vh;
        height: 8vh;
        margin: 1vh;
        fill: rgba($text-color, 0.5);
        z-index: 99;
        background-color: rgba(black, 0.5);
        backdrop-filter: blur(10px);
        border-radius: 50px;

        @media only screen and (max-width: 500px) {
            top: calc(10vh + 15px);
        }

        path{
            fill: inherit;
        }
    }

    .top_bar {
        backdrop-filter: blur(10px);
        height: 8vh;
        position: absolute;
        z-index: 99;
        top: 0;
        color: white;
        background-color: rgba(black, 0.5);
        margin: 1vh;
        border-radius: 10px;
        min-width: 50%;
        display: flex;

        svg {
            width: 25%;
            height: 100%;
            fill: rgba($text-color, 0.5);
            transition: background-color 0.25s ease-out;
            transition: fill 0.25s ease-out;

            path {
                fill: inherit;
            }

            &:hover{
                fill: white;
                background-color: rgba(white, 0.1);
                border-radius: 10px;
            }
        }
    }

    .dir_container {
        background-color: $background;

        padding-top: 10vh;
        padding-bottom: 5vh;

        display: flex;
        flex-wrap: wrap;
        overflow-y: auto;
        gap: 10px;
        justify-content: center;

        .file {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            position: relative;
            width: 200px;
            min-width: 90px;

            &:not(:hover),
            .selected {
                

                svg {
                    fill: rgba($text-color, 0.7);

                    path {
                        fill: rgba($text-color, 0.7);
                    }
                }

                .filename {
                    opacity: 1;
                }
            }

            &:hover,
            &.selected {
                cursor: pointer;
                background-color: rgba(black, 0.5);
                border-radius: 10px;
            }

            &.selected {
                border: white solid 1px;
                
                align-items: center;
                justify-content: center;
                
                .folderOpen{
                    user-select: none;
                    display: flex;
                    position: absolute;
                    color: white;
                    padding: 5% 8%;
                    background-color: $background;
                    border-radius: 50px;
                }
            }

            .folderOpen{
                display: none;
            }

            .filename {
                text-align: center;
                font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
                font-size: 100%;
                color: $text-color;
                font-weight: bold;
                position: absolute;
                bottom: 0;
                width: 100%;
                margin-left: auto;
                margin-right: auto;
                overflow: hidden;
                white-space: nowrap;
                opacity: 0.7;
            }

            svg {

                transition: fill 0.5s ease-in;
                //fill: $background;

                path {
                    transition: fill 0.25s ease-out;
                    //fill: $background;
                }
            }
        }

    }
}